<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    return {
      formCustom: {
        mname: '',
        fname: '',
        sort: ''
      },
      ruleCustom: {
        mname: [
          { required: true, message: '菜单名称不能为空', trigger: 'blur' }
        ],
        fname: [
          { required: true, message: '菜单方法名不能为空', trigger: 'blur' }
        ],
        sort: [
          {  required: true, message: '请输入排序',trigger: 'change' }
        ],
      },
      defaultData: {}
    }
  },
  mounted: function() {
    const token = getToken(),
          mid = this.$route.query.mid || '',
          params = {"token": token, "mid": mid, "type": 2};

    if(mid != '') this.getModelEditDetail(params);
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          console.log(this.formCustom)
          const token = getToken(),
                mid = this.$route.query.mid || '',
                sort = this.formCustom.sort,
                mname = this.formCustom.mname,
                fname = this.formCustom.fname,
                params = {"token": token, "type": 1, "mid": mid, "sort": sort, "mname": mname, "fname": fname};
          
          if(mid != '') {
            const isChange = this.defaultData.name != params.mname || this.defaultData.sort != params.sort || this.defaultData.marks != params.fname;           
            // console.log(isChange, this.defaultData.name != params.mname , this.defaultData.sort != params.sort , this.defaultData.marks != params.fname)
            // console.log(this.defaultData, params)
            if(isChange) {
              this.setAdminGroup(params)
            } else {
              this.$Message.warning('未做任何改变');
            }
          } else {
            this.setAdminGroup(params)
          }
          
        } else {
          // this.$Notice.error({title: res.msg});
        }
      })
    },
    getModelEditDetail (params) {
      api.modelEdit(params).then(res => {
        if(res.code == 0) {
          this.defaultData = res.data
          this.formCustom.mname = res.data.name
          this.formCustom.fname = res.data.marks
          this.formCustom.sort = res.data.sort
        } else {
          this.$Notice.error({
            title: res.msg
          })
        }
      }).catch(err => {
        this.$Notice.error({desc:err});
      })
    },
    setAdminGroup (params) {
      api.modelEdit(params).then(res => {
        if(res.code == 0) {
          this.$Notice.success({
            title: res.msg
          })

          this.$router.push({
            path: '/admin/modelList'
          })
        } else {
          this.$Notice.error({
            title: res.msg
          })
        }
      }).catch(err => {
        this.$Notice.error({desc:err});
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    },
}
}
</script>
<template>
  <div>
    <h3><Icon type="ios-grid-outline" size=24 />编辑菜单</h3>
    <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="100">
        <FormItem label="菜单名称" prop="mname">
          <Input v-model="formCustom.mname"></Input>
        </FormItem>
        <FormItem label="对应方法名" prop="fname">
          <Input v-model="formCustom.fname"></Input>
          <span style="color:orange">请谨慎修改</span>
        </FormItem>
        <FormItem label="排序" prop="sort">
          <Input v-model="formCustom.sort"></Input>
        </FormItem>
        <FormItem>
          <Button type="primary" @click="handleSubmit('formCustom')">提交</Button>
          <Button @click="handleReset('formCustom')" style="margin-left: 8px">重置</Button>
        </FormItem>
    </Form>
  </div>
</template>
<style lang="scss" scoped>
  h3 {
    margin-bottom: 20px;
  }
  .ivu-form {
		width: 20%;
	}
</style>